.navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 60vh;
  background-color: #003b72;
  height: 100vh;
}

a {
  display: flex;
  align-items: center;
  color: white;
  text-decoration: none;
  padding: 14.5px 0;
  transition: 0.2s;
  margin-bottom: 10px;
}

.navbar__btn {
  font-family: "Rubik-Medium";
  display: inline-block;
  width: 125px;
  background-color: #7761ff;
  padding: 10px 0px 10px 25px;
  outline: none;
  user-select: none;
  border-radius: 5px;
  transition: 0.2s;
}

.navbar__btn:hover {
  background-color: white;
  color: black;
}

svg,
img,
.navbar__btn,
.navbar__logo {
  margin-left: 20px;
  margin-right: 12px;
  color: white;
}

.navbar__logo {
  margin-top: 18.5px;
  margin-bottom: 18.5px;
}

.navbar__footer {
  padding-bottom: 15px;
}

a:hover {
  background-color: white;
  color: #7761ff;
  border-left: 3px solid #7761ff;
}

a:hover svg {
  fill: #7761ff;
  margin-left: 17px;
}

.active {
  background-color: #fcfcfc;
  color: #7761ff;
  border-left: 3px solid #7761ff;
}
.active svg {
  fill: #7761ff;
  margin-left: 17px;
}
