.wrapper,
.menu {
  display: flex;
}

.main-content {
  width: 500vh;
  height: 100vh;
}

.menu {
  color: white;
  background-color: #50caff;
  justify-content: space-between;
  height: 54px;
  align-items: center;
}

.menu__title {
  font-size: 28px;
  line-height: 33px;
  font-weight: 500;
  margin-left: 42px;
}

.menu__profile {
  display: flex;
  align-items: center;
  margin-right: 20px;
}

.menu__profile-img {
  border: 2px solid;
  border-radius: 50%;
  border-color: white;
  padding: 1px;
  margin-right: 7px;
  margin-left: 0;
}

.menu__profile svg {
  margin-left: 0px;
  margin-right: 20px;
}
